import React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import Layout from "../components/layout"
import SEO from "../components/seo"

const Blog = () => {
    const data = useStaticQuery(graphql`
        query {
            allContentfulBlog(sort: { fields: publishedDate, order: DESC }) {
                edges {
                    node {
                        id
                        title
                        slug
                        thumbnail {
                            fixed {
                                src
                            }
                        }
                        excerpt {
                            excerpt
                        }
                        publishedDate(fromNow: true)
                    }
                }
            }
        }
    `)
    return (
        <Layout>
            <SEO title="Blog" />
            <div className="blog">
                <label className="title">Blog Archive</label>
                <ul className="row">
                    {data.allContentfulBlog.edges.map(edge => (
                        <li className="column-1" key={edge.node.id}>
                            <Link
                                to={`/blog/${edge.node.slug}`}
                                className="box"
                            >
                                {/* <Img fixed={edge.node.thumbnail.fixed.src} /> */}
                                <h3>{edge.node.title}</h3>
                                <h6>{edge.node.publishedDate}</h6>
                            </Link>
                        </li>
                    ))}
                </ul>
            </div>
        </Layout>
    )
}
export default Blog
